// 
import React, { Component } from 'react'
import { Breadcrumb, Checkbox, Modal, Row, Button, message } from 'antd'
import './nowQuestionnaire.css'
import apis from './../../../subpage/subapi'

class Now extends Component {
  state = {
    showModal2: false,
    showModal3: false,
    list: '', //
    tiHuanList: []
  }

  onChange = () => {
    console.log('')
  }
  onChangeData = e => {
    console.log(`checked = ${e.target.checked}`)
    this.props.history.push('appraise/DataSurvey')
  }
  onChangeTihuan = () => {
    const access_token = window.sessionStorage.getItem('access_token')
    apis
      .get(
        {
          access_token
        },
        '/backend/questionlist/list'
      )
      .then(res => {
        console.log(res)
        if (res.code === 0) {
          this.setState({
            tiHuanList: res.content
          })
        } else {
          message.error(res.message)
        }
      })

    this.setState({
      showModal2: true
    })
  }
  handleSetwenjuqn = () => {
    this.setState({
      showModal2: false
    })
  }
  onChangeCopy = () => {
    this.setState({
      showModal3: true
    })
  }
  handleGobianji = () => {
    this.setState({
      showModal3: false
    })
  }
  handleBack = () => {
    console.log(11)

    // this.props.history.push(-1)
  }
  onChangeEdit = () => {
    this.props.history.push('/edit_module')
  }
  componentDidMount() {
    const access_token = window.sessionStorage.getItem('access_token')
    apis.get({ access_token }, '/backend/questionlist/current').then(res => {
      console.log(res)
      if (res.code === 0) {
        this.setState({
          list: res.content
        })
      } else {
        message.error(res.message)
      }
    })
  }
  render() {
    return (
      <div className="nowquestionnaire">
        <div className="bread-title">
          <div className="bread-title-wrapper">
            <Breadcrumb separator="-">
              <Breadcrumb.Item
                className="bread-pingjia"
                style={{ marginLeft: 10 }}
              >
                
              </Breadcrumb.Item>

              <Breadcrumb.Item></Breadcrumb.Item>
            </Breadcrumb>
          </div>
        </div>
        <div className="bread-content">
          <div className="bread-content-title">
            <h1>APP</h1>
          </div>
          <div className="bread-content-content">
            <div className="child1">
              <div className="left" onClick={this.onChangeData}>{this.state.list.name}</div>
              {/* <div className="left">1</div> */}
              <div className="right">
                <span style={{ marginRight: 10 }}>
                  :{this.state.list.answer_count}
                </span>
                {/* <span style={{marginRight:10}}>:374</span> */}
                <span>:{this.state.list.last_time}</span>
                {/* <span>:2019.9.19</span> */}
              </div>
            </div>
            <div className="child2">
              <div className="left">
                <Checkbox onChange={this.onChangeEdit} checked={false}>
                  
                </Checkbox>
                <Checkbox onChange={this.onChangeData} checked={false}>
                  
                </Checkbox>
              </div>
              <div className="right">
                <Checkbox onChange={this.onChangeTihuan} checked={false}>
                  
                </Checkbox>
                <Checkbox onChange={this.onChangeCopy} checked={false}>
                  
                </Checkbox>
              </div>
            </div>
          </div>
        </div>
        <Modal
          title=""
          visible={this.state.showModal2}
          footer={null}
          onCancel={() => {
            this.setState({
              showModal2: false
            })
          }}
        >
          <Checkbox.Group style={{ width: '100%' }}>
            {this.state.tiHuanList.map((item, index) => {
              return (
                <Row key={index} style={{marginTop:'10px',marginLeft:'30px',fontSize:'24px'}}>
                  <Checkbox value={item.name}>{item.name}</Checkbox>
                </Row>
              )
            })}
            {/* <Row>
              <Checkbox value="B"></Checkbox>
            </Row>
            <Row>
              <Checkbox value="C"></Checkbox>
            </Row>
            <Row>
              <Checkbox value="D"></Checkbox>
            </Row>
            <Row>
              <Checkbox value="E"></Checkbox>
            </Row> */}
            <p className="nowquestionnaire-set">
              <Button type="primary" onClick={this.handleSetwenjuqn}>
                
              </Button>
            </p>
          </Checkbox.Group>
        </Modal>
        <Modal
          visible={this.state.showModal3}
          footer={null}
          width={300}
          onCancel={() => {
            this.setState({
              showModal3: false
            })
          }}
        >
          <p style={{ textAlign: 'center', marginTop: 30 }}></p>
          <p className="nowquestionnaire-set">
            <Button type="primary" onClick={this.handleGobianji}>
              
            </Button>
          </p>
        </Modal>
      </div>
    )
  }
}

export default Now
